<template>
  <div class="withdrawSuccess-box" :class="{iphonexHeight: isIphonex}">
    <Head :title= "title"></Head>
    <div class="withdrawSuccess-center">
      <img class="withdrawSuccess-img" src="../../assets/images/icon-success.png" alt="">
      <p class="withdrawSuccess-text3" v-if="status==1"><br><span>1-3</span><br></p>
      <p class="withdrawSuccess-text3" v-if="status==0"></p>
    </div>
    <div class="withdraw-btn" v-if="status==0">
      <p></p>
    </div>
  </div>
</template>
<script>
  import Ajax from '@/mixins/ajax';
  import Head from '../../components/header/head.vue'
  export default {
    mixins:[Ajax],
    components:{
      Head
    },
    data(){
      return{
        title:'',
        status:0,
      }
    },
    methods:{

    },
    mounted(){
      if(this.$route.query.status){
        this.status=this.$route.query.status
      }
    }
  }

</script>
<style scoped>
  @import "../../style/withdrawInput.css";
</style>




