<template>
  <div class="simpleInf-box" :class="{iphonexHeight: isIphonex}">
    <Head :title= "Title"></Head>
    <div class="head-IFM">
      <span></span>
      <p></p>
    </div>

    <Progress :Protitle="Protitle" :stepForm=2></Progress>
    <div class="simpleInf-form">
      <div class="withdraw-bankInfo">
        <p>
          <label></label>
          <input class="notFill" type="text" placeholder="" v-model="EnglishName" disabled="disabled" @blur="firstUpperCase(EnglishName,1)">
        </p>
        <p>
          <label></label>
          <input type="text" v-model="accountinfo2.sname" placeholder="">
        </p>
        <p>
          <label></label>
          <input type="text" v-model="scityCode" placeholder="" readonly @click='addClick()'>
<!--Start-->
        <Address
          :addShow.sync= "addShow"
          :moduleCounery.sync='moduleCounery'
          :moduleCity.sync='moduleCity'
          :moduleProvince.sync='moduleProvince'
          :moduleDistrict.sync='moduleDistrict'
        />
<!--End-->

        </p>
        <p>
          <label></label>
          <input type="text" v-model="accountinfo2.sadress" placeholder="">
        </p>
        <p>
          <label></label>
          <input type="text" v-model="accountinfo2.szipcode" placeholder="">
        </p>
        <p>
          <label></label>
          <input class="notFill" type="text" placeholder="" disabled="disabled">
        </p>
        <p>
          <label></label>
          <input type="text" v-model="accountinfo2.scardnumber" placeholder="">
        </p>
        <p class="Sel">
          <label></label>
          <select v-model="accountinfo2.sincome">
            <option v-for="(v1,i1) in income" :value="v1.type">{{v1.name}}</option>
          </select>
          <i></i>
        </p>
        <p class="Sel">
          <label>      </label>
          <select v-model="accountinfo2.soccupation">
            <option v-for="(v2,i2) in career" :value="v2.type">{{v2.name}}</option>
          </select>
          <i></i>
        </p>
        <p class="Sel resident">
          <label></label>
          <select v-model="accountinfo2.iamerica">
            <option v-for="(v3,i3) in iamerica" :value="v3.type">{{v3.name}}</option>
          </select>
          <i></i>
        </p>
        <p class="Sel income">
          <label></label>
          <select v-model="accountinfo2.sretaxrevenue">
            <option v-for="(v4,i4) in earn" :value="v4.type">{{v4.name}}</option>
          </select>

          <i></i>
        </p>
      </div>
      <div class="withdraw-btn-box">
        <div class="withdraw-btn" @click="checkForm(EnglishName,scityCode,accountinfo2) ? nextPage() : '' "></div>
      </div>
    </div>
  </div>
</template>

<script>
  import { Ajax } from "@/mixins";
  import Head from '../../components/header/head.vue'
  import Progress from '../../components/pogressBar/progressBar.vue'
  import Address from '../../components/address/address.vue'

  const accountInfo2 = "/api/account/accountinfo/2"

  export default {
//    naem: "rewardPop",
    mixins: [Ajax],
    components: {
      Head,
      Progress,
      Address
    },
    props: {
      // 

    },
    data() {
      return {
        Title: '',
        Protitle: '3 ',
        EnglishName: `${this.$route.query.firstName}${this.$route.query.lastName}`,
//        country: '',
        income: [
          { name: '',type: 0},
          { name: '',type: 1},
          { name: '',type: 2},
          { name: '',type: 3},
          { name: '',type: 4},
          { name: '',type: 5}
        ],
        career: [
          { name: '/',type: 0},
          { name: '',type: 1},
          { name: '',type: 2},
          { name: '',type: 3},
          { name: '',type: 4},
          { name: '',type: 5},
          { name: '',type: 6},
          { name: '',type: 7},
          { name: '',type: 8},
          { name: '',type: 9}
        ],
        earn: [
          { name: '$25,000',type: 0},
          { name: '$25,001-$50,000',type: 1},
          { name: '$50,000-$100,000',type: 2},
          { name: '$100,000',type: 3}
        ],
        iamerica: [
          { name: '',type: 0},
          { name: '',type: 1},
        ],
        CountryS: false,
        citysSync:{},
        addShow:false,
        moduleCounery:'',
        moduleCity:'',
        moduleProvince:'',
        moduleDistrict:'',
//        cityJson:{},
        scityCode:'',
        accountinfo2: {
          sid: '',   //id,,,
          step: 2,   //
          sname: '',   //
          scountry: this.moduleProvince,   //
          sprovincecode: this.moduleCity,   //
          scitycode: this.moduleDistrict,   //
          sadress: '',   //
          szipcode: '',   //
          scertificates: 1,   //1-2-?
          scardnumber: '',  //
          bornDate: '',        //1990-1-2   
          sex: '',   //(0-1-)
          sincome: 0,   //
          soccupation: 0,      //1   
          iamerica: 0,   // 1-0-
          sretaxrevenue: 0   // 0-($25,000),1-($25,001-$50,000),2-($50,000-$100,000),3-($100,000)
        }
      };
    },
    created(){
      this.accUser =  JSON.parse(sessionStorage.getItem("base_info"))
      if(this.accUser){
        this.accountinfo2.sname = this.accUser.sname
        this.accountinfo2.sadress = this.accUser.sadress
        this.accountinfo2.szipcode = this.accUser.szipcode
        this.accountinfo2.scardnumber = this.accUser.scardnumber
        this.accountinfo2.scountry = this.accUser.countryCode
        this.accountinfo2.sprovincecode = this.accUser.provinceCode
        this.accountinfo2.scitycode = this.accUser.scitycode
        this.scityCode = `${this.accountinfo2.scountry}-${this.accountinfo2.sprovincecode}-${this.accountinfo2.scitycode}`
      }
    },
    watch: {
      moduleCounery(nextValue){
        if(nextValue){
          this.scityCode = nextValue
          this.accountinfo2.scountry=this.moduleProvince   //
          this.accountinfo2.sprovincecode=this.moduleCity   //
          this.accountinfo2.scitycode=this.moduleDistrict    //
        }
      }
    },
    methods: {
      addClick: function () {
        this.addShow=true;
          document.getElementsByTagName("body")[0].className="active";   //
      },

      /**/
      firstUpperCase(str,msg) {
        let strs = str.toLowerCase().replace(/( |^)[a-z]/g, (L) => L.toUpperCase());
        if(msg==1){
          this.user.firstName=strs
        }else if(msg==2){
          this.user.lastName=strs
        }
      },

      checkForm: function (EnglishName,scityCode,info) {
        /**/
        var regu = "^(([0-9a-zA-Z]+)|([0-9a-zA-Z]+[_.0-9a-zA-Z-]*[0-9a-zA-Z]+))@([a-zA-Z0-9-]+[.])+([a-zA-Z]{2}|net|NET|com|COM|gov|GOV|mil|MIL|org|ORG|edu|EDU|int|INT)$"
        /**/
        var regname = "^[A-Za-z]+$"
        //
        var regCode=/^\d{6}$/;

        if(!EnglishName){
          this.toast("")
          return false
        }else if(EnglishName.search(new RegExp(regname))){
          this.toast("")
          return false;
        }
        if(!info.sname){
          this.toast("")
          return false
        }else if(!/^[\u2E80-\u9FFF]+$/.test(info.sname)){
          this.toast("")
          return false
        }
        if(!scityCode){
          this.toast("")
          return false
        }
        if(!info.sadress){
          this.toast("")
          return false
        }
        if(!info.szipcode){
          this.toast('')
          return false
        }else if(info.szipcode.search(new RegExp(regCode))){
          this.toast("")
          return false;
        }
        if(!info.scardnumber){
          this.toast("")
          return false
        }else if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(info.scardnumber)){
          this.toast("")
          return false
        }
        return true
      },

      /**/
      nextPage: function () {
        this.accountinfo2.scountry = this.moduleProvince == "" ? this.accUser.countryCode : this.moduleProvince   //
        this.accountinfo2.sprovincecode = this.moduleCity == "" ? this.accUser.provinceCode : this.moduleCity   //
        this.accountinfo2.scitycode = this.moduleDistrict == "" ? this.accUser.scitycode : this.moduleDistrict   //
        this.accountinfo2.sid = this.$route.query.sid

        this.post(accountInfo2, this.accountinfo2).then(body => {
          if(body.code == 200){
            this.$router.push({
              path: '/uploadIdCard',
              query: {
                native: 0,sid:body.data.sid
              }
            })
          }
        });
      },

    },
    mounted(){

    }
  };
</script>

<style scoped>
  .notFill{
    border: none !important;
  }
  input:disabled{
    background: none;
  }
  select{
    background: none;
  }
  .simpleInf-box{
    background: #eee;
  }
  .head-IFM{
    height: 1.79rem;
    padding: 0.64rem 0.56rem 0 0.56rem;
    background: #8e79c0;
    display: flex;
  }
  .head-IFM span{
    display: block;
    width: 1.71rem;
    height: 0.85rem;
    background: url("../../assets/images/right-logo-economic.png") no-repeat center/100% 100%;
  }
  .head-IFM p{
    margin: 0.26rem 0 0 0.3rem;
    font-size: 0.48rem;
    color: #fff;
  }

  .simpleInf-form{
    margin: 0.3rem auto 0 auto;
    padding-bottom: 2.8rem;
    height: 100%;
    background: #fff;
  }

  .withdraw-bankInfo{
    padding:.6rem .54rem 0;
  }
  .withdraw-bankInfo p{
    position: relative;
    height: .9rem;
    line-height: .9rem;
    margin-bottom:.4rem;
  }
  .withdraw-bankInfo label{
    vertical-align: middle;
    font-size:.45rem;
    color: #666;
    min-width:1.9rem;
    display: inline-block;
    text-align: left;
  }
  .withdraw-bankInfo input,.withdraw-bankInfo select{
    display: inline-block;
    width:8.84rem;
    height:.9rem;
    line-height: .9rem;
    font-size:.45rem;
    vertical-align: middle;
    padding:0 .6rem;
    border-bottom:1px solid #ede9f5;
    box-sizing: border-box;
    -webkit-appearance: none
  }
  .withdraw-bankInfo input:focus {
    border-color: #8e79c0;
    border-radius:inherit;
  }
  .withdraw-remark > label{
    vertical-align: top;
  }
  .withdraw-bankInfo textarea{
    width: 8.86rem;
    height: 2.07rem;
    border-radius: .12rem;
    border: solid 1px #d2c9e6;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding:.23rem .6rem;
    font-size:.45rem;
  }

  .withdraw-btn-box{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background: #fff;
  }
  .withdraw-btn{
    width: auto;
    height: 1.32rem;
    line-height: 1.32rem;
    background-color: #8e79c0;
    border-radius: .12rem;
    margin:0 .24rem .6rem .24rem;
    color: #fff;
    font-size:.54rem;
    text-align: center;
  }

  .Sel{
    position: relative;
  }
  .Sel i{
    position: absolute;
    right: 0.5rem;
    bottom: .38rem;
    display: block;
    width: 0.44rem;
    height: 0.24rem;
    background: url("../../assets/images/icon-down.png") no-repeat center/contain;
  }
  .resident select{
    width: 4.48rem !important;
  }
  .income select{
    width: 6.29rem !important;
  }
  input::-webkit-input-placeholder{
    color: #4e5052;
  }


</style>



