<template>
  <div class="userInfo-box" :class="{iphonexHeight: isIphonex}">
    <Head :title= "title"></Head>
    <div class="userInfo-center">
      <p>
        <label></label>
        <input type="text" v-model="user.firstName" placeholder="" @blur="firstUpperCase(user.firstName,1)">
      </p>
      <p>
        <label></label>
        <input type="text" v-model="user.lastName" placeholder="" @blur="firstUpperCase(user.lastName,2)">
      </p>
      <p>
        <label></label>
        <input type="text" v-model="user.sname" placeholder="">
      </p>
      <p>
        <label></label>
        <input type="email" v-model="user.semail" placeholder="">
      </p>
      <p>
        <label></label>
        <span>{{userJson.userPhone}}</span>
      </p>
      <p class="userInfo-city">
        <label></label>
        <input type="text" v-model="citys" unselectable="on" onfocus="this.blur()" placeholder="" readonly @click='addShow=true;cityJson={}'>
        <Address
          :addShow.sync= "addShow"
          :moduleCounery.sync='moduleCounery'
          :moduleCity.sync='moduleCity'
          :moduleProvince.sync='moduleProvince'
          :moduleDistrict.sync='moduleDistrict'
        />
      </p>
      <p>
        <label></label>
        <input type="text" v-model="user.sadress" placeholder="">
      </p>
      <p>
        <label></label>
        <input type="text" v-model="user.szipcode" maxlength=6 placeholder="">
      </p>
      <p>
        <label></label>
        <span></span>
      </p>
      <p>
        <label></label>
        <input type="text" v-model="user.scardnumber" maxlength=18 placeholder="">
      </p>
      <div class="uploadImg">
        <p>
          <img @click="UploadIdCard('getUserCardPhotoZ')" :src="user.scardfphoto" alt="" :onerror="defaultImg(0)">
        </p>
        <p>
          <img @click="UploadIdCard('getUserCardPhotoF')" :src="user.scardlphoto" alt="" :onerror="defaultImg(1)">
        </p>

      </div>
    </div>
    <div class="withdraw-btn" >
      <p @click="inputVerify(user,1)"></p>
    </div>
  </div>
</template>

<script>
  import { Ajax } from "@/mixins";
  import Head from '../../components/header/head.vue'
  import Address from '../../components/address/address.vue'

  export default {
     mixins: [Ajax],
    components: {
      Head,
      Address
    },
    data() {
      return {
        title: '',
        type: 1,    //
        addShow:false,
        citys:'',
        moduleCounery:'',
        moduleCity:'',
        moduleProvince:'',
        moduleDistrict:'',
        user:{
          firstName:'',
          lastName:'',
          sname: '',
          semail:'',//
          sadress: '',//
          szipcode: '',//
          scertificates: 1 ,//(1-2-?)
          scardnumber: '' ,// 
          scardfphoto: '',//
          scardlphoto: '',// 
          countryCode: this.moduleProvince,    //
          provinceCode: this.moduleCity,    //
          scitycode: this.moduleDistrict,     //
        },
      };
    },
    beforeRouteEnter(to, from, next) {
      next(vm => {
        vm.init(to, from);
      });
    },
    watch:{
      moduleCounery(nextValue){
        if(nextValue){
          this.citys = nextValue
          this.user.countryCode=this.moduleProvince   //
          this.user.provinceCode=this.moduleCity   //
          this.user.scitycode=this.moduleDistrict    //
        }
      }
    },
    created(){

    },
    methods: {
      init(to, from) {
        //    console.log(invitingPop)
        //

        if (from.path == "/userInfoVerify") {
          this.user=JSON.parse(sessionStorage.getItem("user_data"))
          this.citys = `${this.user.countryCode}-${this.user.provinceCode}-${this.user.scitycode}`
        }
      },
      firstUpperCase(str,msg) {
        let strs = str.toLowerCase().replace(/( |^)[a-z]/g, (L) => L.toUpperCase());
        if(msg==1){
          this.user.firstName=strs
        }else if(msg==2){
          this.user.lastName=strs
        }

      },
      inputVerify(user,msg){
        /**/
        var regu = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/;
        /**/
        var regname =/^[A-Za-z]+$/;
        //
        var reg=/^\d{6}$/;
        //
        var regRule = /\uD83C[\uDF00-\uDFFF]|\uD83D[\uDC00-\uDE4F]/g;
        //
        var regIDcard=/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;

        if(!user.firstName || !user.lastName){
          this.toast('')
          return false
        }else if(!regname.test(user.firstName) || !regname.test(user.lastName)){
          this.toast("")
          return false;
        }else if(regRule.test(user.firstName) || regRule.test(user.lastName)){
          this.toast("")
          return false;
        }

        if(!user.sname){
          this.toast('')
          return false
        }else if(regRule.test(user.sname)){
          this.toast("")
          return false;
        }
        if(!user.semail){
          this.toast('')
          return false
        }else if(!regu.test(user.semail)){
          this.toast("")
          return false;
        }

        if(!user.countryCode || !user.provinceCode || !user.scitycode){
          this.toast('')
          return false;
        }

        if(!user.sadress){
          this.toast('')
          return false
        }else if(regRule.test(user.sadress)){
          this.toast("")
          return false;
        }

        if(!user.szipcode){
          this.toast('')
          return false
        }else if(!reg.test(user.szipcode)){
          this.toast("")
          return false;
        }

        if(!user.scardnumber){
          this.toast('')
          return false
        }else if(!regIDcard.test(user.scardnumber)){
          this.toast("")
          return false;
        }

        if(!user.scardfphoto || !user.scardlphoto){
          this.toast('')
          return false
        }

        if(msg){
          this.user.citys=this.citys
          sessionStorage.setItem("user_data", JSON.stringify(this.user))
          this.$router.push({path: '/userInfoVerify',query:{native: 0}})
        }
      },
    },

//  {"imageUrl":"/q/q/1231.jpg","state":"getUserCardPhotoZ"}
    mounted(){
      this.$bus.on('updateImg', para => {
        let Url = {}
        Url = JSON.parse(para)
        if(Url.state == 'getUserCardPhotoZ'){
          this.user.scardfphoto = Url.imageUrl
        }else if(Url.state == 'getUserCardPhotoF'){
          this.user.scardlphoto = Url.imageUrl
        }
      });
      this.user.semail = this.userJson.userEmail
    },
    destroyed(){
      this.$bus.off('updateImg')
    }
  };
</script>

<style scoped>
@import "../../style/withdrawInput.css";

.userInfo-center{
  padding:.8rem 0 2rem;
}
.userInfo-center > p{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding:0 .53rem;
  height:1.4rem;
}
.userInfo-center > p label{
  font-size:.48rem;
  color: #666;
  height:.8rem;
}
.userInfo-center > p span,.userInfo-center p input{
  width:8.9rem;
  height:.8rem;
  line-height: .8rem;
  font-size: .48rem;
  padding:0 .3rem;
  display: inline-block;
}
.userInfo-center > p span{
  color: #000;
}
.userInfo-center > p input{
  border-bottom:1px solid #ede9f5;
  -webkit-appearance: none
}
.userInfo-center > p input:focus{
  border-color: #8e79c0;
  border-radius:inherit;
}
.userInfo-city input{
  background: url("../../assets/images/icon-right.png") no-repeat right center/.23rem .4rem;
}
</style>



